import {profileAPI} from "../api/api";

const ADD_POST = 'ADD-POST';

const SET_USER_PROFILE = 'SET_USER_PROFILE-NEW-POST-TEXT';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';


let initialState = {
    posts: [
        {id: 1, message: "Hi! It's my first post!", likes: 211},
        {id: 2, message: "Is anybody here?", likes: 8},
        {id: 3, message: "Hello?.....", likes: 1},
        {id: 4, message: "All alone.......", likes: 0},],
    profile: null,
        status: ""
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let text = action.textArea
            return {
                ...state,
                newPostText: '',
                posts: [...state.posts, {id: 5, message: text, likes: 0}]
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
            case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
            case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(p => p.id !== action.postId)
            }

            case SAVE_PHOTO_SUCCESS:
            return {
                ...state,
                profile: {...state.profile, photos: action.photos}}

        default:
            return state;
    }
}

export const addPostActionCreator = (textArea) => ({type: ADD_POST, textArea})
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setStatus = (status) => ({type: SET_STATUS, status})
export const deletePost = (postId) => ({type: DELETE_POST, postId})
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO_SUCCESS, photos})

export const getUserProfile = (userId) => async (dispatch) => {
    let data = await profileAPI.getProfile(userId)
        dispatch(setUserProfile(data));

}

export const getStatus = (userId) => async (dispatch) => {
     let data = await profileAPI.getStatus(userId)
        dispatch(setStatus(data));

}

export const updateStatus = (status) => async (dispatch) => {
    try {
        let response = await profileAPI.updateStatus(status)
        if (response.data.resultCode === 0) {
            dispatch(setStatus(status));
        }
    } catch (error) {

    }
}

export const savePhoto = (file) => async (dispatch) => {
    let response = await  profileAPI.savePhoto(file)
         if (response.data.resultCode === 0) {
             dispatch(savePhotoSuccess(response.data.data.photos));
         }
}

export const saveProfile = (profile, setStatus) => async (dispatch, getState) => {
    const userId = getState().auth.userId
    let response = await  profileAPI.saveProfile(profile, setStatus)
         if (response.data.resultCode === 0) {
             dispatch(getUserProfile(userId));
         } else {

             setStatus({message: response.data.messages})
         }
}


export default profileReducer;